:root {
  --bg-color: #fff;
  --text-color: #111;
  --accent-color: #007acc;
  --card-bg: #f9f9f9;
  --card-text: #333;
  --card-border: #ccc;
  --btn-bg: #eee;
  --btn-hover: #ddd;
  --btn-text: #000;
  --btn-delete: #f44336;
  --btn-delete-hover: #d32f2f;
}

.dark {
  --bg-color: #1e1e1e;
  --text-color: #ddd;
  --accent-color: #4ea1f3;
  --card-bg: #2e2e2e;
  --card-text: #eee;
  --card-border: #555;
  --btn-bg: #444;
  --btn-hover: #555;
  --btn-text: #fff;
  --btn-delete: #e53935;
  --btn-delete-hover: #c62828;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  margin: 0;
  padding: 1rem;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  transition: background-color .3s, color .3s;
}

header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  display: flex;
}

button {
  background-color: var(--accent-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: .5rem 1rem;
}

button:hover {
  opacity: .9;
}

section {
  margin-bottom: 2rem;
}

.url-input {
  margin-bottom: 1rem;
}

.param-row {
  gap: .5rem;
  margin-bottom: .5rem;
  display: flex;
}

.param-row input {
  flex: 1;
  padding: .3rem;
}

.param-row button {
  background-color: var(--btn-delete);
  padding: .2rem .5rem;
}

#url-main-input {
  border: 1px solid var(--border-color);
  background: var(--bg-color);
  width: auto;
  min-width: 200px;
  color: var(--text-color);
  border-radius: .3rem;
  flex: 1;
  margin-right: .5rem;
  padding: .3rem .5rem;
}

#rebuilt-url {
  cursor: pointer;
  user-select: all;
  transition: color .2s;
}

#rebuilt-url.copied {
  color: green;
  font-weight: bold;
}

.url-card {
  border: 1px solid var(--card-border);
  background-color: var(--card-bg);
  border-radius: 12px;
  flex-direction: column;
  gap: .5rem;
  width: 100%;
  max-width: 380px;
  padding: 1rem;
  display: flex;
  box-shadow: 0 2px 6px #0000000d;
}

.url-cards {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-top: 1.5rem;
  display: flex;
}

.url-card__label {
  border: 1px solid var(--border-color);
  background: var(--bg-color);
  color: var(--text-color);
  box-sizing: border-box;
  border-radius: 6px;
  width: 100%;
  padding: .5rem;
  font-size: 1rem;
}

.url-card__url-value {
  cursor: pointer;
  word-break: break-all;
  overflow-wrap: break-word;
  box-sizing: border-box;
  border-radius: 6px;
  padding: .5rem;
  font-size: .9rem;
}

.url-card__tags {
  box-sizing: border-box;
  border: 1px solid var(--border-color);
  background: var(--bg-color);
  width: 100%;
  color: var(--text-color);
  border-radius: 6px;
  margin-top: .25rem;
  padding: .5rem;
  font-size: .9rem;
}

.url-card__actions {
  flex-shrink: 0;
  gap: .5rem;
  display: flex;
}

.url-card__button {
  cursor: pointer;
  background-color: var(--btn-bg);
  color: var(--btn-text);
  border: none;
  border-radius: 6px;
  padding: .5rem;
  font-size: .9rem;
  transition: background-color .2s, color .2s;
}

.url-card__button:hover {
  background-color: var(--btn-hover);
}

.url-card__button--delete {
  background-color: var(--btn-delete);
  color: #fff;
}

.url-card__button--delete:hover {
  background-color: var(--btn-delete-hover);
}

#url-list {
  padding: 1rem;
}

#url-list button {
  background-color: var(--btn-bg);
  color: var(--btn-text);
  cursor: pointer;
  opacity: 1;
  border: none;
  border-radius: 6px;
  margin-right: .5rem;
  padding: .4rem .8rem;
  font-size: .9rem;
  transition: background-color .2s;
}

.language-switcher {
  margin-right: 1rem;
}

.search-container {
  margin-bottom: 1rem;
}

.search-container input {
  width: 50%;
}

#tag-filters button {
  background-color: var(--btn-bg);
  color: var(--btn-text);
  border: 1px solid var(--btn-hover);
  cursor: pointer;
  border-radius: 12px;
  padding: .3rem .7rem;
  font-size: .9rem;
  transition: all .2s;
}

#tag-filters {
  flex-wrap: wrap;
  gap: .5rem;
  margin-bottom: 1rem;
  display: flex;
}

#tag-filters button:hover {
  background-color: var(--btn-hover);
  border-color: var(--accent-color);
}

#url-list button:hover {
  background-color: var(--btn-hover);
}

@media (width <= 480px) {
  .url-card {
    max-width: 100%;
    padding: .8rem;
  }

  .url-card button, .search-container input {
    width: 100%;
  }
}
